body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

header {
  height: 60px;
  border-bottom: 1px solid white;
  margin-bottom: 10px;
  display: flex;
  justify-content: space-between;
  align-content: center;
}

.title {
  font-size: 25px;
  padding: 10px;
  margin-top: 8px;
  font-family: 'Times New Roman', Times, serif;
}

.btn-restart {
  margin-top: 15px;
  margin-left: 10px;
  margin-bottom: 15px;
  background-color: white;
  color: blue;
  border: 1px solid;
  border-radius: 5px;
  height: 30px;
  font-size: 20px;
}
.card {
  margin-top: 15px;
  margin-bottom: 15px;
  width: 200px;
  height: 250px;
  opacity: 1;
}

.hidden {
  opacity: 0;
}

.back {
  width: 200px;
  height: 250px;
  border: 3px solid blue;
  border-radius: 10px;
  background-image: url(/memoryCards/static/media/backBlue.f7898002.png);
  display: flex;
  justify-content: center;
  align-items: center;
}

.card-content {
  width: 200px;
  height: 250px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  border: 1px solid;
  border-radius: 15px;
}

.React-FlipCard {
  width: 100%;
  height: 100%;
}
.board {
  background: white;
  margin: 30px auto;
  padding: 0px 33px 0px 25px;
  border-radius: 10px;
  color: brown;
  display: flex;
  flex-wrap: wrap;
  height: 630px;
  width: 900px;
  justify-content: space-around;
}
body {
  background-color: darkgreen;
  color: white;
}
